<script>
import numeral from 'numeral';
export default ({
    props: {
        viewKey: String,
        dialogView:Boolean,
    },
    methods: {
      numberFormat(value){
          return numeral(value).format('0,0')
      },

      getNames(){
          this.dialogW=true
              this.axios
              .post("cart/get/request", { data: { book: this.viewKey, type:'view'} })
              .then(response => {this.items = response.data.data,this.allData = response.data, this.dialogW=false
              })
              .catch(() => {
              });
      },
    },
    data () {
    return {
        allData:[],
        items:[],
        dialogW:false,
    }},
     watch:{
        dialogView(){
            this.getNames()
        },
    },
    computed: {
        dialog: {
        get () {
            return this.dialogView
        },
        set (value) {
            this.$emit('update:dialogView', value)
        }
        },

    },



})
</script>
