<template>
<div>
  <v-data-table
    :headers="headers"
    :items="filteredRequests"
    :loading = "isLoadingSearch"
    loading-text="Loading... Please wait"
    sort-by="calories"
    class="elevation-1"
    dense
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title>        
            <v-select
              class="pl-1"
              v-model="show_mode"
              :items="status_tabele"
              item-text="name"
              item-value="id"
              label="Status"
              single-line
              hint="Select filter "
              persistent-hint
          ></v-select>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
        <v-card>
          <v-card-title class="red-text ">
            Do you want to Accept?
          </v-card-title>
          <v-card-text>By clicking on "Agree", you are declaring that you will provide all the services in a respectful manner without any changes to the previously agreed EatInMore policies.</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="red darken-1"
              text
              @click="save(2,'Pending')"
            >
              Disagree
            </v-btn>
            <v-btn
              color="green darken-1"
              text
              @click="save(3, 'Accepted')"
            >
              Agree
            </v-btn>           
            <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Close
              </v-btn>
          </v-card-actions>
        </v-card>

        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.status="{ item }">
        <v-btn
        elevation="1"
        @click="editItem(item)"
        class="ma-2"
        color="green"
        x-small
        v-if="item.status==='3' | item.status===3"
        >
            Accepted
        </v-btn>
        <v-btn
        elevation="1"
        @click="editItem(item)"
        class="ma-2"
        x-small
        v-else-if="item.status==='4' | item.status===4"
        >
            Pending payment
        </v-btn>
        <v-btn
        elevation="1"
        @click="editItem(item)"
        class="ma-2"
        x-small
        v-else-if="item.status==='5' | item.status===5"
        color="green"

        >
            Payment accepted
        </v-btn>
        <v-btn
        elevation="1"
        class="ma-2"
        x-small
        v-else-if="item.status==='6'  | item.status=== 6 "
        color="green"
        >
            Confirmed service
        </v-btn>

        <v-btn
        @click="editItem(item)"
        class="ma-2"
        x-small
        v-else
        >
            Pending
        </v-btn>
    </template>

    <template v-slot:item.title="{ item }">
        <v-chip
        class="ma-2"
        x-small
        >
            {{item.title}}
        </v-chip>
    </template>

    <template v-slot:item.boking_amount="{ item }">
            {{item.alphabetic_code}} {{numberFormat(item.boking_amount)}}
    </template>

    <template v-slot:item.cook_start_date="{ item }">
        <v-btn
            icon
            color="primary"
            @click="view(item)"
            class="ma-2"
        >
            <v-icon>mdi-eye</v-icon>
        </v-btn>
        {{item.cook_start_date}}
    </template>

    <template v-slot:item.order_id="{ item }">
        <router-link :to="'/pub-order/'+item.key" class="pt-3">
            {{item.order_id}}
        </router-link>
    </template>

  </v-data-table>
    <viewRequest :viewKey="viewKey" :dialogView.sync="dialogView"/>
  </div>
</template>


<script>
import numeral from 'numeral';
import viewRequest from '../Cart/view.vue';

  export default {
    components: {viewRequest},
    data: () => ({
      dialogView:false,
      dialog: false,
      viewKey:'',
      dialogDelete: false,
      headers: [
            { text: 'OrdeID', value: 'order_id' },
            { text: 'Status', value: 'status', sortable: false },
            { text: 'Date', value: 'cook_start_date' },
            { text: 'Amount', value: 'boking_amount' },
            {
            text: 'Last Update',
            align: 'start',
            //sortable: false,
            value: 'updated_at',
            },
        // { text: 'Time', value: 'Cook_start_time' },
        // { text: 'Currency', value: 'alphabetic_code' },
            { text: 'Country', value: 'entity' },
            { text: 'State', value: 'state_name' },
        // { text: 'Address', value: 'full_address' },
        // { text: 'Category', value: 'title' },
            //{ text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      user_location:'',
      editedIndex: -1,
      editedItem: {
        status: 2,
      },
      defaultItem: {
        status: 2,
      },
      listPrimitive: null,
      isLoadingSearch: true,
      requestList:[],
      status:[
            {name:"Not Accepted",id:"2"},
            {name:"Accepted",id:"3"},
        ],
      status_tabele:[
            {name:"Pending",id:"2"},
            {name:"Accepted",id:"3"},
            {name:"Pending payment",id:"4"},
            {name:"Payment accepted",id:"5"},
            {name:"Confirmed service",id:"6"},
            {name:"Payment exception",id:"7"},
        ],
      show_mode:"2",
      user_id:'',
    }),

    computed: {
        formTitle () {
            return this.editedIndex === -1 ? 'New Item' : 'Accept Request'
        },

        filteredRequests() {
            let requestList = this.desserts
            //console.log(requestList)
            if(parseInt(this.show_mode) != 2){
              requestList = requestList.filter((item) => {
              return (parseInt(item.chef_id_confirmation) === this.user_id)
              })
            }
            
            requestList = requestList.filter((item) => {
            return (parseInt(item.status) === parseInt(this.show_mode))
            })
            
            return requestList;
        },

    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created() {
    },

    methods: {
       view(viewKey){
           this.dialogView = true;
           this.viewKey = viewKey.key;
       },

      numberFormat(value){
          return numeral(value).format('0,0')
      },

        
        initialise() {
            this.axios
            .get('booking/GetRequests')
            .then(response => (
                this.requestList = response.data.requests,
                this.desserts = this.requestList,
                this.isLoadingSearch = false
            ));

        },



        getrRequests (){
             this.axios
                .get('booking/GetRequests')
                .then(
                    response => (
                        this.listPrimitive.set(response.data.requestes)
                    )
                );
        },
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save (status,status_name) {
        //verify if user have permission to update
        this.editedItem.status = status
        this.editedItem.status_name = status_name

        this.axios
        .post('booking/confirm-chef-booking',{ data: this.editedItem })
         .then(response => {
          this.allerros = [];
          this.sucess = true;
          this.saveAction(response.data);
        })

        .catch(() => {
            this.success = false;
        });

      },

      saveAction (){
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
          //this.listPrimitive.update(this.editedIndex, this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
          //this.listPrimitive.add(this.editedItem)
        }
        //this.listPrimitive.set(response)
        this.close()

      },
    },


    mounted: function () {
        this.axios
        .get('booking/GetRequests')
        .then(response => (
            //primitive.set(response.data),
            this.desserts = response.data.requests,
            this.isLoadingSearch = false,
            this.user_location = response.data.user_location,
            this.user_id = response.data.user_id
            //this.subscribeToUpdate()
        ));

     
    },
  }
</script>
